<style scoped>
  .uk-dropdown-nav {
      font-size: .975rem;
  }



  .uk-input, .uk-select:not([multiple]):not([size]) {
      height: 35px;
      vertical-align: middle;
      display: inline-block;
  }

  .uk-grid-small > div{
    margin-bottom:0px;
  }

  .uk-form-label {
      font-size: .56rem;
      text-transform: uppercase;
      letter-spacing: 1px;
  }

</style>


<template>

    <div style="font-size: 0.7rem;background: #fbfbfb;;display:grid;grid-template-columns:1fr;width:100%;place-items:start center;overflow-y:overlay" class="custom-scroll-bar">



      <div class="stage" style="display: grid;place-items: stretch;grid-template-rows: 40px 1fr;padding: 15px;overflow-y: hidden;color: rgb(51, 51, 51);">

          <div class="adk_grid_toolbar" style="grid-template-columns:auto 1fr;margin-right: 50px;margin-left: 50px;border-bottom: 1px solid #e0e0e0;">

              <div style="display: grid;place-self: center start;text-align: left;">
                  <div style="display:grid;place-self:center start;padding-left:0px;padding-right:20px;color:rgba(61, 61, 61, 0.78) !important;grid-template-columns:auto auto;font-size: 1rem;line-height: 35px;">
                     <span style="font-size: 1rem; font-weight: bold;letter-spacing:0.5px">
                      Edit Customer
                     </span>
                  </div>
              </div>
              <div style="display: grid;grid-gap: 10px;grid-template-columns: auto auto;place-self: center end;text-align: right;">
              </div>
          </div>

          <div class="" style="padding-top: 30px;padding-left: 50px;padding-right: 50px;">

              <form autocomplete="off" class="uk-grid-small uk-grid" uk-grid onsubmit="return false;">

                  <div class="uk-width-1-2@s uk-first-column">
                      <label class="uk-form-label" for="form-stacked-text">Customer</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  tabindex="5"  class="uk-select" v-model="customerContact.customerId" v-on:change="loadFilteredBranches()" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <template v-bind:key="item.id" v-for="item,index in availableCustomers">
                                      <option v-bind:value="item.id">{{item.name}}</option>
                                  </template>
                              </select>
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin">
                      <label class="uk-form-label" for="form-stacked-text">Branch</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  tabindex="5"  class="uk-select" v-model="customerContact.customerBranchId" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <template v-bind:key="item.id" v-for="item,index in filteredCustomerBranches">
                                      <option v-bind:value="item.id">{{item.name}}</option>
                                  </template>
                              </select>
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s  uk-grid-margin uk-first-column ">
                      <label class="uk-form-label" for="form-stacked-text">First Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="1" id="firstName" type="text" class="uk-input"  placeholder=""  v-model="customerContact.firstName" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div  class="uk-width-1-2@s uk-grid-margin">
                      <label class="uk-form-label" for="form-stacked-text">Last Name</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="1" id="lastName" type="text" class="uk-input"  placeholder=""  v-model="customerContact.lastName" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column ">
                      <label class="uk-form-label" for="form-stacked-text">Primary Email</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="4" id="primaryEmail" type="text" class="uk-input"  placeholder=""  v-model="customerContact.primaryEmail" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin">
                      <label class="uk-form-label" for="form-stacked-text">Alternate Email</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="4" id="alternateEmail" type="text" class="uk-input"  placeholder=""  v-model="customerContact.alternateEmail" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column ">
                      <label class="uk-form-label" for="form-stacked-text">Primary Phone no:</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="4" id="primaryPhoneNumber" type="text" class="uk-input"  placeholder=""  v-model="customerContact.primaryPhoneNumber" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin">
                      <label class="uk-form-label" for="form-stacked-text">Alternate Phone no:</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <input tabindex="4" id="alternatePhoneNumber" type="text" class="uk-input"  placeholder=""  v-model="customerContact.alternatePhoneNumber" style="font-family: Lato;font-weight: 300;" autofocus="true">
                          </div>
                      </div>
                  </div>

                  <div class="uk-width-1-2@s uk-grid-margin uk-first-column">
                      <label class="uk-form-label" for="form-stacked-text">Country</label>
                      <div class="uk-form-controls">
                          <div class="uk-inline uk-width-1-1">
                              <select  tabindex="5"  class="uk-select" v-model="customerContact.country" style="font-family: Lato;font-weight: 300;height: 35px;">
                                  <option value="Australia">Australia</option>
                                  <option value="Botswana">Botswana</option>
                                  <option value="Canada">Canada</option>
                                  <option value="India">India</option>
                                  <option value="USA">USA</option>
                              </select>
                          </div>
                      </div>
                  </div>
                  <div class="uk-width-1-2@s uk-grid-margin"></div>

                  <div class="uk-margin uk-grid-margin uk-first-column">
                      <div class="uk-inline">
                          <a tabindex="6" id="saveButton" class="clickable-btn uk-button uk-button-danger uk-button-small uk-grid-margin uk-first-column end-call-button" style="background-color: rgb(2 119 254); border-radius: 2px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="save()" >Save</a>
                          &nbsp;&nbsp;&nbsp;
                          <!--<a tabindex="7" class="clickable-btn uk-button uk-button-danger uk-button-small uk-first-column end-call-button" style="background-color: rgba(253, 253, 253, 0.99); color: rgb(111, 111, 111); border: 1px solid rgb(183, 183, 183); border-radius: 3px; place-self: center start; place-items: center; min-width: 100px; font-size: 0.65rem; line-height: 32px; font-weight: normal !important;" v-on:click="goBack()" >Exit</a>-->
                      </div>
                  </div>
                </form>

            </div>
      </div>
    </div>
</template>


<script>



import userMixinLib               from './../../mixins/lib/user_mixin_lib';
import utilsMixinLib              from './../../mixins/lib/utils_lib';
import uiListMixinLib             from './../../mixins/lib/ui-list.js';

    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib,
                        uiListMixinLib
                    ],
        props: ["id"],
        data: function () {
            return {
                appName: "taskone",
                tenantName : this.$route.params.tenant_name,
                customerContact :{
                    id : this.id,
                    type : null,
                    status : null,
                    memberIds : [],
                    adminIds : []
                },
                availableCustomers : [],
                availableCustomerBranches : [],
                filteredCustomerBranches : []
            };
        },
        methods: {

            goBack(){
              this.$router.push({
                  name: 'customer-contacts-list',
                  params: {
                  }
              });
            },
            doValidation(){

                console.log("this.customerContact = ", this.customerContact);
                if(this.customerContact.firstName.trim()==="")
                {
                      UIkit.notification(`<div class="taskone-notification">
                                              <span uk-icon="icon: warning;ratio:1"></span>
                                              <div> Please enter a valid First Name. </div>
                                          </div>`, {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.customerContact.lastName.trim()==="")
                {
                      UIkit.notification(`<div class="taskone-notification">
                                              <span uk-icon="icon: warning;ratio:1"></span>
                                              <div> Please enter a valid Last Name. </div>
                                          </div>`, {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }
                else if(this.customerContact.customerId===null || this.customerContact.customerId===undefined)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please select a valid Customer.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            save(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.


                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let post_url = "./customer-contacts/save";
                console.log("Posting data to : " + post_url);

                let form = {
                    id :      this.customerContact.id,
                    customerId :   this.customerContact.customerId,
                    customerBranchId :   this.customerContact.customerBranchId,
                    firstName :   this.customerContact.firstName,
                    lastName : this.customerContact.lastName,
                    primaryEmail  :   this.customerContact.primaryEmail,
                    alternateEmail :   this.customerContact.alternateEmail,
                    primaryPhoneNumber  :   this.customerContact.primaryPhoneNumber,
                    alternatePhoneNumber :   this.customerContact.alternatePhoneNumber,
                    country : this.customerContact.country
                }

                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("CustomerContact Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                this.newCustomerInfo = dataResponse.data["bean"];

                                UIkit.notification(`<div class="taskone-notification">
                                                        <span uk-icon="icon: check;ratio:1"></span>
                                                        <div> Contact updated successfully. </div>
                                                    </div>`, {
                                                    status: 'success',
                                                    pos: 'bottom-left',
                                                    timeout: 500000
                                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                this.$emit("refreshList",{});
                            }
                            else
                            {
                                let errorMsg = (dataResponse["data"])["message"];
                                UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + errorMsg, {
                                                    status: 'danger',
                                                    pos: 'top-right',
                                                    timeout: 5000
                                                });


                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                return false;
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.data["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.75'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },

            // Handle the events emitted when selectbox component values are modified.
            updateMembersEmitEvent(data){
                const memberIds = Array.from(data).map(item => item.id);
                this.customerContact["memberIds"] = memberIds;
            },
            updateAdminsEmitEvent(data){
                const adminIds = Array.from(data).map(item => item.id);
                this.customerContact["adminIds"] = adminIds;
            },
            getRecord(){

                let get_url = "./customer-contacts/get/" + this.customerContact.id;

                axios.get(get_url)
                        .then((dataResponse) => {
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let customerContact = dataResponse.data["bean"];
                                this.customerContact = customerContact;

                                // Since in the initial state of the form, the customer selectbox will be at the default first value,
                                // we are invoking this method directly to load its corresponding branches.
                                this.loadFilteredBranches();
                            }
                        })
                        .catch((error) => {

                            UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + error, {
                                                status: 'danger',
                                                pos: 'bottom-left',
                                                timeout: 5000
                                            });
                            return false;
                        });


            },

            // This data will be used to filter both members and admins by merging with the result from 'get-associated-people/{category-id}'
            loadCustomers(){
                let url = "./customers/list/all";          // Fetch all potential members (users)
                this.getUnpaginatedList("customers", url);   // This lib call will fire the callback "handleUnpaginatedListData" when it completes.
            },
            // This data will be used to filter both members and admins by merging with the result from 'get-associated-people/{category-id}'
            loadCustomerBranches(){
                let url = "./customer-branches/list/all";          // Fetch all potential members (users)
                this.getUnpaginatedList("customer-branches", url);   // This lib call will fire the callback "handleUnpaginatedListData" when it completes.
            },

            // Callbacks of loadPotentialUsers function.
            handleUnpaginatedListData(listKey, data){
                if(listKey === "customers")
                  this.handleCustomersListData(data);
                else if(listKey === "customer-branches")
                  this.handleCustomerBranchesListData(data);
            },
            handleUnpaginatedListDataError(error){
                console.error("Unpaginated List data fetch error : ", error);
            },

            //Callbacks
            handleCustomersListData(dataResponse){
                //Pass it to the availableMembers prop to the dropdown.
                console.log("Customers : ", dataResponse);

                let mapFunction = (item) => {
                    return {
                        id        : item.id,
                        name      : item.name
                    };
                };

                let mappedArrayOfItem = dataResponse.data.bean.map(mapFunction);
                this.availableCustomers = mappedArrayOfItem;

                console.log("this.availableCustomers : ", this.availableCustomers);
            },
            handleCustomerBranchesListData(dataResponse){
                //Pass it to the availableCustomerBranches prop to the dropdown.
                console.log("Customer Branches : ", dataResponse);

                let mapFunction = (item) => {
                    return {
                        id        : item.id,
                        name      : item.name,
                        customerId      : item.customerId
                    };
                };

                let mappedArrayOfItem = dataResponse.data.bean.map(mapFunction);
                this.availableCustomerBranches = mappedArrayOfItem;

                console.log("this.availableCustomerBranches : ", this.availableCustomerBranches);

                // Since in the initial state of the form, the customer selectbox will be at the default first value,
                // we are invoking this method directly to load its corresponding branches.
                this.loadFilteredBranches();
            },
            loadFilteredBranches(){

                let filteredBranches = [];

                console.log("this.availableCustomerBranches : ", this.availableCustomerBranches, "this.customerContact.customerId : ", this.customerContact.customerId);
                // Filter the branches based on the selected customer
                this.availableCustomerBranches
                    .filter(branch => branch.customerId===this.customerContact.customerId)
                    .forEach(filteredBranch => filteredBranches.push(filteredBranch));

                this.filteredCustomerBranches = filteredBranches;
                console.log("this.filteredCustomerBranches : ", this.filteredCustomerBranches);
            }

        },
        mounted: function () {
          this.loadCustomers();
          this.loadCustomerBranches();
        },
        created: function () {
            console.log("Edit customer form");
        },
        watch: {

          // Since we are passing an asynced data as 'availableItems', it is necessary to watch this prop when it receives new data.
          id:function(newId, oldId){
            console.log("id changed from " + oldId + " to " + newId);
            this.customerContact.id = newId;
            this.getRecord();
            // :selected="customerContact.customerId===item.id"
          }
        }
    }

</script>
