                                <ul class="uk-nav-sub" style="max-height: 300px;height: 300px;overflow-y: auto;">
                                    <template v-for="category in myCategoriesList">
                                        <li class="menu-item" v-bind:id="'cat_' + category.id">
                                          <a v-on:click="loadTasksFromCategory(category)">{{category.name}}
                                            <span class="counter-label" v-bind:id="'cat_count_'  + category.id">0</span>
                                          </a>
                                        </li>
                                    </template>
                                </ul>
                            </li>
                        </ul>

                    </div>
                    <!--
                    <div style="border-right: 1px solid #ccc;">
                        <div style="color: #333!important;font-weight: 400!important;font-size: .7rem;display: flex!important;align-items: center;padding: 5px 10px;">
                            <a href="/admin" style="text-decoration:none"><span class="lnr lnr-cog"></span>&nbsp;&nbsp;Settings</a>
                        </div>
                    </div>
                    -->
                  </div>
              </div>
              <div id="appContainer" class="app-container-div" style="flex-grow: 1;width: 100%;">
                  <div id="notificationsPanel" class="notifications_panel hide_notification_panel" style="">
                      <notification-panel></notification-panel>
                  </div>
                  <div id="appsPanel" class="apps_panel hide_apps_panel" style="padding:10px;;align-items:center;justify-content:start">
                    <div style="display: flex;flex-direction: column;gap: 10px;align-items: center;justify-content: center;background: #4CAF50;color: rgb(255 255 255);padding: 10px;border-radius: 3px;font-size: 0.75rem;cursor: pointer;">
                        <div>
                          <span uk-icon="world" v-on:click="openBusinessBench()" class="uk-icon" ></span>
                        </div>
                        <div>
                          <a href="/businessbench" style="text-decoration:none;width: 65px;text-align: center;color: white;">Business Bench</a>
                        </div>
                    </div>
                  </div>
                  <router-view v-if="loggedInUser.isInformationFetchComplete" v-bind:loggedInUser="loggedInUser"></router-view>
              </div>
          </div>
          <!--
          <div v-if="loggedInUser.isInformationFetchComplete"  class="app_footer">
              <div class="control_bar_buttons" style="float: right;padding-right: 10px;place-self: center end;color: #fff;">Task<span style="color: #23a9ec;">One</span><span style="color: #fff;"> apps</span></div></div>
          </div>
          -->
    </div>
</template>

<script>



import {bus} from './../config_app.js';
import userMixinLib    from './../mixins/lib/user_mixin_lib';
import rsocket    from './../mixins/lib/rsocket';

export default {

    mixins : [userMixinLib, rsocket],

    name : 'app',
    data : function(){
        return {
            isChatViewOn : false,
            loggedInUser : {
              isInformationFetchComplete:false
            },
            isLoggingOut : false,
            isOnline : false,
            toggleOnlineText : "OFFLINE",
            toggleOnlineCommandText : "Go Online.",
            chatImage : "./files/displayPicture-ff8081813a3a5174013a3a8d71c40002.png",
            hasProfileImage : true,
            uploadedPicId : "",
            showMenu1 : false,
            showMenu2 : false,
            showMenu3 : false,
            isMobileDevice : false,
            resizeTimeout : null,
            notificationsSocket : null,
            notificationSocketReconnectionAttemptCount : 0,

            myCategoriesList : [],
            isCategorySubmenuOpened : false,

            rsocket :{
              socket :null,     //This will be populated with the connected socket from the 'connected-rsocket' bus event listener.
            },
            rsocketConnectedNotificationDisplayed : false,
            rsocketDisconnectedNotificationDisplayed : false,
            newNotificationsCounter : 0,

            isNewTaskOptionsOpen : false
        }
    },
    mounted : async function(){


        //console.log("adk-app.vue mounted.");

        /* December 5, 2020

        This code is very important. The data declared and assigned within mixins is not reflected reactively to other
        components that import the mixin. So even if the socket is initialzed within the mixin, it needs to be broadcasted to other components
        which wants to use it in its business logic.

        For example, in task-inbox.vue, it uses the rsocket to set the category-channel-id for receiving activity updates.
        In this component, if we don't listen to the bus event ('connected-rsocket') and assign the connected socket,
        the below function 'connectToNotificationSocket' will throw an error as 'socket is null'.

        Hence this assignment in every component is very important and mandatory.
        */
        bus.on("connected-rsocket", (socket)=>{
            this.rsocket.socket = socket;
        });

        bus.on("refresh-task-counts", ()=>{
          this.fetchTaskCounts();
        });

        // Increment the Notification Label Count
        bus.on("new-notification", (data)=> {

            if(this.newNotificationsCounter > 10)
            {
              this.newNotificationsCounter = "10+";
            }
            else
            {
              this.newNotificationsCounter += 1;
            }

            var thissound = document.getElementById("notification_sound");
                thissound.volume = 1;
                thissound.play();
        });

        // Receive the chat-message from chat-page.vue component and send it to the server via rsocket.
        bus.on("send-chat-msg-to-server", (data) => {
            this.sendChatMessageToServer(data);
        });

        await this.getSessionInformation();
        let rsocketConnectionResult = await this.connectToRSocket();
        console.log("rsocket-conn : ", rsocketConnectionResult);
        if(rsocketConnectionResult==="rsocket-connected")
        {
            document.querySelector("#notification_rt_status").classList.remove("soc_disconnected");
            document.querySelector("#notification_rt_status").classList.add("soc_connected");
        }
        this.connectToNotificationSocket();

        let checkSocketStatus = (socket) => socket.availability();
        setInterval(async() => {

           let socketAvailability = checkSocketStatus(this.rsocket.socket);
           if(socketAvailability === 0)
           {
               console.error("Looks like the socket connection is closed. Retrying to connect.");

               if(!this.rsocketDisconnectedNotificationDisplayed)
               {
                   UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Disconnected from server.", {
                                       status: 'danger',
                                       pos: 'bottom-left',
                                       timeout: 5000
                                   });
                   this.rsocketDisconnectedNotificationDisplayed = true;
                   this.rsocketConnectedNotificationDisplayed = false;    //Only if this is set to false, its if condition will become true when connected again.
               }

               document.querySelector("#notification_rt_status").classList.add("soc_disconnected");
               document.querySelector("#notification_rt_status").classList.remove("soc_connected");

               // Retry connection code to be written here..
               let rsocketConnectionResult = await this.connectToRSocket();
               console.log("rsocket-conn after reattempt : ", rsocketConnectionResult);

               // Once socket connection is restored, connect to the NotificationSocket for receiving regular heartbeats.
               if(rsocketConnectionResult.trim() === "rsocket-connected.")
               {
                   console.log("Connecting to notification-realtime");
                   this.connectToNotificationSocket();
               }

           }
           else if(socketAvailability===1)
           {
               if(!this.rsocketConnectedNotificationDisplayed)
               {
                   UIkit.notification(`<div class="taskone-notification">
                                           <span uk-icon="icon: link;ratio:1"></span>
                                           <div> Connected to server. </div>
                                       </div>`, {
                                       status: 'success',
                                       pos: 'bottom-left',
                                       timeout: 5000
                                   });
                   this.rsocketConnectedNotificationDisplayed = true;
                   this.rsocketDisconnectedNotificationDisplayed = false;    //Only if this is set to false, its if condition will become true when disconnected again.
               }
               document.querySelector("#notification_rt_status").classList.remove("soc_disconnected");
               document.querySelector("#notification_rt_status").classList.add("soc_connected");


           }

        }, 5000);


        //Frequent Data Caching APIs
        let result = await this.fetchCustomersForCaching();
        console.log("Customers fetch result", result);


        let result3 = await this.fetchCustomerBranchesForCaching();
        console.log("Customer Branches fetch result", result3);

        let result2 = await this.fetchMyCategories();
        console.log("MyCategories fetch result", result2);

        // Position the context menus
        let primaryButton = document.getElementById("new_task_button");
        let primaryButtonOptions = document.getElementById("new_task_button_options");
        let bcr = primaryButton.getBoundingClientRect();
        //console.log("primaryButton's BCR ", bcr);
        primaryButtonOptions.style.top  = bcr.top + bcr.height + 2 + "px";
        primaryButtonOptions.style.left = bcr.left + "px";

    },
    methods:{

        switchApp(appName){

            if(appName === "settings")
              window.location = "/admin";
            else if(appName === "tasks")
              window.location = "/app";
            else if(appName === "channels")
              window.location = "/channels";
            else if(appName === "calendar")
              window.location = "/calendar";
            else if(appName === "businessbench")
              window.location = "/businessbench";
            else if(appName === "dashboard")
              window.location = "/dashboard";
        },
        closeNewTaskOptions(){
            this.isNewTaskOptionsOpen = false;
            //This means, if target on which the click happened is not within the context-menu div, so you can close the context-menu dropdown.
            let primaryButtonOptions = document.getElementById("new_task_button_options");
            primaryButtonOptions.style.transform = null;

            //Remove click listener
            document.querySelector('body').removeEventListener('click', this.listenForBodyClicks, false);
            console.log("body click event removed.");
        },
        listenForBodyClicks(event){

            console.log("Body clicked at target ", event.target);

            let foundParent  = event.target.closest('.new_task_options');
            let foundTrigger = event.target.closest('.new_task_options_trigger_button');  // The click event on the trigger must also be ignored.

            if(foundParent === null && foundTrigger===null) {
              console.log("Closing menu as the body-click event landed on the body");
              this.closeNewTaskOptions();
            }
        },
        handleNewTaskOptionsClick(){

            console.log("handleNewTaskOptionsClick clicked : ");

            if(!this.isNewTaskOptionsOpen){
                console.log("body click event added.");
                this.isNewTaskOptionsOpen = true;
                let primaryButtonOptions = document.getElementById("new_task_button_options");
                primaryButtonOptions.style.transform = "scale(1)";

                console.log("menu scaled to open.");
                document.querySelector('body').addEventListener('click', this.listenForBodyClicks, false);
            }
            else{
                this.closeNewTaskOptions();
            }
        },
        createTask(taskType){

            console.log("Creating task of type : " + taskType);
            console.log("Event emitted in bus.", new Date());

            if(taskType === "template")
                bus.emit("newTemplateTaskEvent", {});
            else if(taskType === "scheduled")
                bus.emit("newScheduledTaskEvent", {});
            else if(taskType === "recurring")
                bus.emit("newRecurringTaskEvent", {});

            this.closeNewTaskOptions();
        },
        showOrHideChat(menuFilter){
          if(!this.isChatViewOn)
            this.signalShowChat(menuFilter);
          else
            this.signalHideChat();
        },
        signalShowChat(menuFilter){


            /*
            document.querySelectorAll(".menu-item").forEach((el)=>{
              el.classList.remove("uk-active");
              el.classList.remove("selected-menu");}
            );
            document.querySelector("#" + menuFilter).classList.add("selected-menu");
            document.querySelector("#" + menuFilter).classList.add("uk-active");
            */

            if(!this.isChatViewOn)
            {
                this.isChatViewOn = true;
                bus.emit("show-chat",{});
            }

            // If the current view is any other route, then switch to inbox view.
            if(this.$route.name!=='view-task-inbox')
            {
                this.$router.push({
                    name: "view-task-inbox",
                    params: {
                    }
                });
            }


        },
        signalHideChat(){


            this.isChatViewOn = false;
            bus.emit("hide-chat",{});
        },
        toggleNotificationsPanel(){

            document.querySelector("#notification_trigger").classList.toggle("notification_trigger_inactive");
            document.querySelector("#notification_trigger").classList.toggle("notification_trigger_active");
            document.querySelector("#notificationsPanel").classList.toggle("show_notification_panel");
            document.querySelector("#notificationsPanel").classList.toggle("hide_notification_panel");

        },
        toggleAppsPanel(){

            document.querySelector("#apps_trigger").classList.toggle("apps_trigger_inactive");
            document.querySelector("#apps_trigger").classList.toggle("apps_trigger_active");
            document.querySelector("#appsPanel").classList.toggle("show_apps_panel");
            document.querySelector("#appsPanel").classList.toggle("hide_apps_panel");

        },

        logoutFromHere(){ //new
            document.getElementById("loform").submit();
        },
        // Any updates received in the rsocket from server, will be sent to the same component from where the rsocket initially
        // registered and connected to.
        // For example, In this adk-app.vue component, on the mounted() listener, we initiated the call for rsocket to connect.
        // from task-inbox, we used the rsocket to connect to a category-channel. But when an update for that category-channel
        // was pushed from server to rsocket, then rsocket cannot invoke a function in the task-inbox to pass those updates.
        // Hence we first receive the update in this adk-app.vue component and then pass it to the task-inbox component via the event-bus.
        // This needs good blogging inorder to avoid confusions with using mixins to centralize certain objects like rsocket.socket.
        sendUpdateToTaskInboxComponent(payload){
            console.log("payload received in adk-app.vue");
            bus.emit("task-activity-update", payload);
        },

        toggleCategoryMenu(){
            this.isCategorySubmenuOpened = !this.isCategorySubmenuOpened;

            if(this.isCategorySubmenuOpened)
            {
                document.querySelector("#category-submenu").style.display=null;
                document.querySelector("#category-menu-trigger").style.transform="rotateX(-180deg)";
            }
            else
            {
                document.querySelector("#category-submenu").style.display="none";
                document.querySelector("#category-menu-trigger").style.transform="rotateX(0deg)";
            }

        },
        sendNewTaskEvent(){
          console.log("Event emitted in bus.", new Date());
          bus.emit("newTaskEvent", {});
        },
        sendNewChatEvent(){
          console.log("Event emitted in bus.", new Date());
          bus.emit("newChatEvent", {});
        },
        goTo(routeName) {
            this.$router.push({
                name: routeName,
                params: {}
            });
        },
        profileImageLoadError(){
          this.hasProfileImage = false;
        },
        uploadPic(fieldName, fileList){


          let _this_component = this;
           // handle file changes
           const formData = new FormData();
              formData.append("name", this.loggedInUser.userId);


           if (!fileList.length) return;

           // append the files to FormData
           Array.from(Array(fileList.length).keys())
                .map(x => {
                   formData.append(fieldName, fileList[x], fileList[x].name);
                });

           // Display the key/value pairs
           for(var pair of formData.entries()) {
              console.log(pair[0]+ ', '+ pair[1]);
           }

          var url =  '/profile/upload-my-dp';
          var xhr = new XMLHttpRequest();
            xhr.open('POST', url);
              xhr.setRequestHeader("ENCTYPE","multipart/form-data");
              //xhr.setRequestHeader("Content-Type","multipart/form-data;boundary=CUSTOM");
              xhr.send(formData);


            // Setup our listener to process completed requests
              xhr.onload = function() {

                // Process our return data
                if (xhr.status >= 200 && xhr.status < 300) {
                  // This will run when the request is successful
                  console.log('success!', xhr);
                  let savedImageName = xhr["responseText"];
                  console.log("savedImageName=" + savedImageName);
                  _this_component.uploadedPicId = savedImageName;

                  //$("#uploadedImage").attr("src",);
                  document.querySelector("#uploadedImage","/profile/get/" + savedImageName);

                  self.postMessage("Processed data from Worker.");
                } else {
                  // This will run when it's not
                  console.log('The request failed!');
                }

                // This will run either way
                // All three of these are optional, depending on what you're trying to do
                //console.log('This always runs...');
              };

        },
        goToPage : function(pageKey,params, id){


            this.signalHideChat();

            document.querySelectorAll(".menu-item").forEach((el)=>{
              el.classList.remove("uk-active");
              el.classList.remove("selected-menu");
            });
            document.querySelector("#" + id).classList.add("uk-active");
            document.querySelector("#" + id).classList.add("selected-menu");

            this.$router.push({
                name: pageKey,
                params: params
            });
        },
        goToHomePage : function(){

            window.location = "/app";
            return false;

            this.$router.push({
                name: 'home-page',
                params: {
                }
            });
        },
        fetchMyCategories(){

            return new Promise(resolve => {

                  let url = "./categories/list/mine";

                  try
                  {
                      axios.get(url)
                          .then((dataResponse) => {
                              //console.log("My Category List: " , dataResponse);
                              this.myCategoriesList = dataResponse.data.bean;
                              this.fetchCategoryTaskCounts(this.myCategoriesList);
                              this.fetchTaskCounts();

                              resolve("fetched");
                          })
                          .catch((error) => {
                              console.error("Error while fetching my-categories-list : ",error);

                              resolve("fetch my-categories failed");
                          });
                  }
                  catch(error)
                  {
                      console.error("Unpaginated List data fetch error : ", error);
                  }
            });

        },
        fetchCategoryTaskCounts(categoryList){

            //http://localhost:9090/tasks/tasks-count-by-category/5f82a94ad0885b18f91b6176,5f85e128212c14629bfad08c
            let categoryListCSV = categoryList.map((item) => item.id)
                                              .join(",");

            return new Promise(resolve => {

                  let url = "./tasks/tasks-count-by-category/" + categoryListCSV;

                  try
                  {
                      axios.get(url)
                          .then((dataResponse) => {
                              //console.log("My Category Task Counts: " , dataResponse);
                              //this.myCategoriesList = dataResponse.data.bean;
                              dataResponse.data.forEach((countObj) => {
                                  document.getElementById("cat_count_" + countObj["id"]).innerHTML = "("+ countObj["taskCount"] + ")";
                              });

                          })
                          .catch((error) => {
                              console.error("Error while fetching my-categories-taskcounts : ",error);
                          });
                  }
                  catch(error)
                  {
                      console.error("Unpaginated List data fetch error : ", error);
                  }
            });
        },
        loadTasksFromCategory(category){

            this.signalHideChat();
            document.querySelectorAll(".menu-item").forEach((el)=>{el.classList.remove("uk-active");el.classList.remove("selected-menu");});
            document.querySelector("#cat_" + category.id).classList.add("selected-menu");
            bus.emit('filterByCategory', category.id);
        },
        fetchTaskCounts(){

            //http://localhost:9090/tasks/tasks-count-by-category/5f82a94ad0885b18f91b6176,5f85e128212c14629bfad08c

            return new Promise(resolve => {

                  let url = "./tasks/tasks-count-by-filters";

                  try
                  {
                      axios.get(url)
                          .then((dataResponse) => {
                              console.log("My Task Counts: " , dataResponse);

                              dataResponse.data.forEach((countObj) => {

                                  if(countObj["filterName"]==="inbox" && countObj["taskCount"]>0)
                                    document.getElementById("inbox_count").innerHTML = "("+ countObj["taskCount"] + ")";
                                  else if(countObj["filterName"]==="sent" && countObj["taskCount"]>0)
                                    document.getElementById("sent_count").innerHTML = "("+ countObj["taskCount"] + ")";
                                  else if(countObj["filterName"]==="completed" && countObj["taskCount"]>0)
                                      document.getElementById("completed_count").innerHTML = "("+ countObj["taskCount"] + ")";
                                  else if(countObj["filterName"]==="overdue" && countObj["taskCount"]>0)
                                    document.getElementById("overdue_count").innerHTML = "("+ countObj["taskCount"] + ")";
                                  else
                                    console.error("Filter not configured for '" + countObj["filterName"] + "'");
                              });

                          })
                          .catch((error) => {
                              console.error("Error while fetching my-categories-taskcounts : ",error);
                          });
                  }
                  catch(error)
                  {
                      console.error("Unpaginated List data fetch error : ", error);
                  }
            });
        },
        loadFilteredTasks(taskFilter, menuFilter){

            this.signalHideChat();

            // If the current view is any other route, then switch to inbox view.
            if(this.$route.name!=='view-task-inbox')
            {
                this.$router.push({
                    name: "view-task-inbox",
                    params: {
                    }
                });
            }


            document.querySelectorAll(".menu-item").forEach((el)=>{
              el.classList.remove("uk-active");
              el.classList.remove("selected-menu");}
            );
            document.querySelector("#" + menuFilter).classList.add("selected-menu");
            document.querySelector("#" + menuFilter).classList.add("uk-active");
            bus.emit('filterByTaskStatus', taskFilter);
        },
        loadInbox(filter){

            this.signalHideChat();

            // Route --> #/task/inbox/
            console.log("this.$route = ", this.$route);

            // If the current view is any other route, then switch to inbox view.
            if(this.$route.name!=='view-task-inbox')
            {
                this.$router.push({
                    name: "view-task-inbox",
                    params: {
                    }
                });
            }

            document.querySelectorAll(".menu-item").forEach((el)=>{
              el.classList.remove("uk-active");
              el.classList.remove("selected-menu");
            });
            document.querySelector("#" + filter).classList.add("uk-active");
            document.querySelector("#" + filter).classList.add("selected-menu");
            bus.emit('filterByCategory', "all");
        },

        fetchCustomersForCaching(){

            return new Promise(resolve => {

                  let url = "./data-caching/customers/list";

                  try
                  {
                      axios.get(url)
                          .then((dataResponse) => {
                              console.log("Customers List: " , dataResponse);
                              localStorage.setItem("customers", JSON.stringify(dataResponse.data,null,null));
                              resolve("fetched");
                          })
                          .catch((error) => {
                              console.error("Error while fetching my-categories-list : ",error);
                              resolve("fetch failed");
                          });
                  }
                  catch(error)
                  {
                      console.error("Unpaginated List data fetch error : ", error);
                  }
            });

        },
        fetchCustomerBranchesForCaching(){

            return new Promise(resolve => {

                  let url = "./data-caching/customer-branches/list";

                  try
                  {
                      axios.get(url)
                          .then((dataResponse) => {
                              console.log("Customer Branches List: " , dataResponse);
                              localStorage.setItem("customer_branches", JSON.stringify(dataResponse.data,null,null));
                              resolve("fetched");
                          })
                          .catch((error) => {
                              console.error("Error while fetching customer-branches/list : ",error);
                              resolve("fetch failed");
                          });
                  }
                  catch(error)
                  {
                      console.error("Unpaginated List data fetch error : ", error);
                  }
            });

        },

    },

    created : function(){




    }


}
</script>

