                      <ui-icon name="contacts" size="width:16px;height:16px"/>
                      <span v-show="taskObject.transitionCount!==undefined && taskObject.transitionCount!==null && taskObject.transitionCount>0" style="background: rgb(240 170 80);color: white;padding: inherit;border-radius: 15px;width: 25px;text-align: center;">{{taskObject.transitionCount}}</span>
                    </span>
                    <span class="tab_label">Contacts</span></a>
                </li>
                <li uk-tooltip="title:Intermediaries;pos:bottom">
                  <a >
                    <span class="tab_icon" style="display: flex;column-gap: 5px;">
                      <ui-icon name="intermediaries" style="width: 16px; height: 16px;"/>
                      <span v-show="taskObject.attachedFormsCount!==undefined && taskObject.attachedFormsCount!==null && taskObject.attachedFormsCount>0" style="background: rgb(240 170 80);color: white;padding: inherit;border-radius: 15px;width: 25px;text-align: center;">{{taskObject.attachedFormsCount}}</span>
                    </span>
                    <span class="tab_label">Intermediaries</span></a>
                </li>
              </ul>
              <div class="uk-switcher uk-switcher-component" style="margin-top: 0px;font-size: 0.7rem;padding:0px 0px 20px 0px;overflow-y: hidden;display: grid;display:flex;flex-grow: 1;background: rgb(249, 250, 255);touch-action: pan-y pinch-zoom;">
                <div style=";flex-grow: 1;overflow-y: hidden; position: relative;">
                    <div style="display: grid;grid-template-rows: 1fr;display:flex;flex-grow: 1;padding: 20px;height: 100%;overflow-y:hidden">
                        <customer-activity-tab v-bind:id="customerObject.id"/>
                    </div>
                </div>
                <div style=";flex-grow: 1;overflow-y: hidden; position: relative;    padding: 20px;">

                      <div  v-show="customerObject.tasks!==undefined && customerObject.tasks.length>0"  class="subtask_row" style="background: #f2f2f2;border: 1px solid transparent;border-bottom: 0px;text-transform: uppercase;font-size: 0.55rem;">
                          <div style=";justify-self: end;">Task No.</div>
                          <div >Title</div>
                          <div  style="color: rgb(51, 51, 51);">Due Date</div>
                          <div  style="color: rgb(51, 51, 51);">Status</div>
                      </div>
                      <div v-show="customerObject.tasks!==undefined && customerObject.tasks.length>0"  class="activity_box">

                          <template v-for="subtask in customerObject.tasks">

                              <div class="subtask_row" >
                                <div style="font-weight:normal;justify-self: end;"><a v-on:click="viewSubTask(subtask.id)">#{{subtask.sno}}</a></div>
                                <div style="cursor:pointer" v-on:click="viewSubTask(subtask.id)">{{subtask.name}}</div>
                                <div style="color:#333">{{subtask.dueDateTime}}</div>
                                <div>
                                  <div v-if="subtask.status!==null">
                                      <span v-if="subtask.status=='In progress'" style="color: rgb(46 166 253);text-transform: uppercase;font-size: 0.6rem;letter-spacing: 0.5px;">{{subtask.status}}</span>
                                      <span v-else-if="subtask.status=='Completed'" style="color:rgb(39 199 0);text-transform: uppercase;font-size: 0.6rem;letter-spacing: 0.5px;">{{subtask.status}}</span>
                                      <span v-else style="color: rgb(46 166 253);text-transform: uppercase;font-size: 0.6rem;letter-spacing: 0.5px;">{{subtask.status.split("#")[1]}}</span>
                                  </div>
                                </div>
                              </div>

                          </template>
                      </div>
                </div>
                <div style=";flex-grow: 1;overflow-y: hidden; position: relative;;padding: 20px;">
                      Documents
                </div>
                <div style=";flex-grow: 1;overflow-y: hidden; position: relative;height: 100%;;padding: 20px;">
                      Recurring Tasks
                </div>
                <div style="overflow-y: hidden; position: relative;;padding: 20px;">
                      Scheduled Tasks
                </div>

                <div style="overflow-y: hidden; position: relative;width: 100%;;padding: 20px;">
                      Contacts
                </div>

                <div style=";flex-grow: 1;overflow-y: hidden; position: relative;padding: 20px;">
                    Intermediaries
                </div>

              </div>
        </div>
    </div>
  </div>
</template>


<script>



  import userMixinLib               from './../../mixins/lib/user_mixin_lib';
  import utilsMixinLib              from './../../mixins/lib/utils_lib';
  import uiListMixinLib             from './../../mixins/lib/ui-list.js';

    export default{
        mixins :    [
                        userMixinLib,
                        utilsMixinLib,
                        uiListMixinLib
                    ],
        props: ["id"],
        data: function () {
            return {
                appName: "taskone",
                tenantName : this.$route.params.tenant_name,
                customerObject :{
                    id : this.id,
                    type : null,
                    status : null,
                    memberIds : [],
                    adminIds : [],
                    taskCount : 0
                },
                availableCategories : [],
                availableMembers : [],
                availableAdmins : [],
                taskObject :{
                    id : this.id,
                    remindersCount : 0,
                    timelogsCount : 0,
                    transitionCount : 0,
                    attachedFormsCount : 0
                },
                updateTaskInfoEnabled : false,
            };
        },
        methods: {

            updateCategoryEmitEvent(data){
                if(data!=null)
                  this.customerObject["categoryId"] = data.id;
                else
                  this.customerObject["categoryId"] = null;

            },
            goBack(){
              this.$router.push({
                  name: 'customers-list',
                  params: {
                  }
              });
            },
            doValidation(){

                console.log("this.customerObject = ", this.customerObject);
                if(this.customerObject.name.trim()==="")
                {
                      UIkit.notification(`<div class="taskone-notification">
                                              <span uk-icon="icon: warning;ratio:1"></span>
                                              <div> Please enter a valid Name. </div>
                                          </div>`, {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 500000
                                      });

                      return false;
                }
                else if(this.customerObject.type===null)
                {
                      UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span> Please select a valid Type.", {
                                          status: 'danger',
                                          pos: 'bottom-left',
                                          timeout: 5000
                                      });

                      return false;
                }

                return true;
            },
            save(){

                if(!this.doValidation())
                    return false; // stop here if form is invalid.


                let btnText = document.getElementById("saveButton").innerHTML;
                document.getElementById("saveButton").innerHTML = "Saving..";
                this.disableHTMLElement(document.getElementById("saveButton"));

                let post_url = "./customers/save";
                console.log("Posting data to : " + post_url);

                let form = {
                    id :      this.customerObject.id,
                    name :   this.customerObject.name,
                    categoryId  :   this.customerObject.categoryId,
                    type  :   this.customerObject.type,
                    contactName :   this.customerObject.contactName,
                    contactEmail :this.customerObject.contactEmail,
                    country : this.customerObject.country
                }

                // VueJS ajax call-1
                axios.post(post_url, form)
                          .then((dataResponse) => {

                            console.log("Customer Registration Result : ");
                            console.log(dataResponse);

                            if(dataResponse.data["actionResult"]===1)
                            {
                                this.newCustomerInfo = dataResponse.data["bean"];

                                UIkit.notification(`<div class="taskone-notification">
                                                        <span uk-icon="icon: check;ratio:1"></span>
                                                        <div>Customer updated successfully. </div>
                                                    </div>`, {
                                                    status: 'success',
                                                    pos: 'bottom-left',
                                                    timeout: 5000
                                                });

                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                this.$emit("refreshList",{});
                            }
                            else
                            {
                                let errorMsg = (dataResponse["data"])["message"];
                                UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + errorMsg, {
                                                    status: 'danger',
                                                    pos: 'top-right',
                                                    timeout: 5000
                                                });


                                document.getElementById("saveButton").innerHTML = btnText;
                                this.enableHTMLElement(document.getElementById("saveButton"));
                                return false;
                            }


                    })
                    .catch(function(errorResponse) {

                        console.log("ERROR MS - ", errorResponse);
                        let exceptionMsg = errorResponse.data["exception"];

                        UIkit.notification("<span uk-icon='icon: warning ;ratio: 0.75'></span> " + exceptionMsg + ".", {
                                            status: 'danger',
                                            pos: 'top-right',
                                            timeout: 500000
                                        });


                        document.getElementById("saveButton").innerHTML = btnText;
                        this.enableHTMLElement(document.getElementById("saveButton"));
                        return false;
                    });
            },

            // Handle the events emitted when selectbox component values are modified.
            updateMembersEmitEvent(data){
                const memberIds = Array.from(data).map(item => item.id);
                this.customerObject["memberIds"] = memberIds;
            },
            updateAdminsEmitEvent(data){
                const adminIds = Array.from(data).map(item => item.id);
                this.customerObject["adminIds"] = adminIds;
            },
            getRecord(){

                let get_url = "./customers/get/" + this.customerObject.id;

                axios.get(get_url)
                        .then((dataResponse) => {
                            if(dataResponse.data["actionResult"]==1)
                            {
                                let customerObject = dataResponse.data["bean"];
                                this.customerObject = customerObject;

                                if(customerObject.tasks!==undefined)
                                  this.customerObject["taskCount"] = customerObject.tasks.length;

                                this.updateCategoryList();  // This function resets the checkboxes of the category values and checks the values based on the currently viewed customer
                            }
                        })
                        .catch((error) => {

                            UIkit.notification("<span uk-icon='icon: warning;ratio: 0.75'></span>" + error, {
                                                status: 'danger',
                                                pos: 'bottom-left',
                                                timeout: 5000
                                            });
                            return false;
                        });


            },
            // This data will be used to filter both members and admins by merging with the result from 'get-associated-people/{workgroup-id}'
            loadPotentialCategories(){
                let url = "./bb-categories/list/all";          // Fetch all potential members (users)
                this.getUnpaginatedList("potentialCategories", url);   // This lib call will fire the callback "handleUnpaginatedListData" when it completes.
            },
            // Callbacks of loadPotentialUsers function.
            handleUnpaginatedListData(listKey, data){
                if(listKey === "potentialUsers")
                  this.handlePotentialUsers(data);
                else if(listKey === "potentialCategories")
                  this.handlePotentialCategories(data);
            },
            handleUnpaginatedListDataError(error){
                console.error("Unpaginated List data fetch error : ", error);
            },
            handlePotentialCategories(dataResponse){
                //Pass it to the availableAssignees prop to the dropdown.
                //console.log("Potential Categories : ", dataResponse);

                let mapFunction = (item) => {
                    return {
                        id        : item.id,
                        name      : item.name,
                        isSelected: false
                    };
                };

                let mappedArrayOfCategory = dataResponse.data.bean.map(mapFunction);

                let businessBenchCategories=[];
                mappedArrayOfCategory.forEach((item)=>{

                    let catObj = {
                        id : item.id,
                        name : item.name,
                        isSelected : false // Set false by default, so that if the workgroup didn't have any member, then all of the available list could be passed to selectbox.
                    };

                    if(this.customerObject.categoryId!==null)
                    {
                        if(catObj.id===this.customerObject.categoryId)
                          catObj["isSelected"] = true;
                    }
                    businessBenchCategories.push(catObj);
                });

                this.availableCategories = businessBenchCategories;
                //console.log("this.availableAssignees : ", this.availableCategories);
            },
            updateCategoryList(){


                console.log("this.availableCategories" , this.availableCategories, this.customerObject);

                this.availableCategories.forEach((item)=>{
                    item["isSelected"] = false;   // Reset the values by default and mark it as checked only if it passes the below IF condition.

                    if(item.id===this.customerObject.categoryId){
                      console.log("Found matching category id : ", item.id);
                      item["isSelected"] = true;
                    }

                });

                console.log("this.availableCategories" , this.availableCategories);

                // The following is a hack to refresh the dropdown-list's values.
                // The child component's props get updated only when the prop itself changes and not its child props.
                // Hence we kind of reset the availableCategories list as below.
                let categories = [];
                this.availableCategories.forEach((item) => categories.push(item));
                this.availableCategories = [];
                this.availableCategories = categories;
            },

        },
        mounted: function () {

          this.loadPotentialCategories();

        },
        created: function () {
            console.log("Edit customer form");
        },
        watch: {

          // Since we are passing an asynced data as 'availableItems', it is necessary to watch this prop when it receives new data.
          id:function(newId, oldId){
            console.log("id changed from " + oldId + " to " + newId);
            this.customerObject.id = newId;
            this.getRecord();
          }
        }
    }

</script>
