         chosenFileName : null,
         isFileUploading : false,
         uploadActivityFile : false,
         currentlyUploadedFile : null
      }
    },
    methods: {

      alterArrayObjectValue(){
        //this.chosenFiles[0].isUploaded = true;
      },

